.section-title {
    margin: 0;
    margin-bottom: 48px;
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;

    @include on-tablet {
      margin-bottom: 56px;
      font-size: 40px;
    }

    @include on-desktop {
      font-size: 48px;
    }

  &--margin--bottom {
    margin-bottom: 24px;

    @include on-tablet {
      margin: 0 24px 0 0;
    }

    @include on-desktop {
      margin: 0;
    }
  }
}
